.site-header {
  padding: 15px 30px;

  background: var(--light-blue);

  @media(min-width: 1400px) {
    padding: 0;
  }
}

.site-nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  height: 100%;

  @media(min-width: 1400px) {
    display: grid;

    grid-template-columns: 1fr 210px 1fr;
  }
}

// Menu
.menu {
  display: flex;
  order: 3;
  width: 100%;
  margin-top: 20px;
  padding: 10px 0;

  border-top: 1px solid rgba(68, 174, 181, 0.2);
  border-bottom: 1px solid rgba(68, 174, 181, 0.2);

  @media(min-width: 1024px) {
    order: 1;
    width: auto;
    margin: 0;
    padding: 0;

    border: none;
  }

  li {
    margin-right: 30px;

    @media(min-width: 1024px) {
      margin-right: 64px;
    }


    &:last-child {
      margin-right: 0;
    }
  }
}

.menu__link {
  font-size: var(--fz);
  line-height: 20px;
  color: var(--accent-color);
  text-decoration: none;

  &:hover,
  &:focus,
  &:active {
    text-decoration: underline;
  }
}

// Logo
.logo {
  order: 1;

  @media(min-width: 1400px) {
    margin: -11px auto 0;
  }

  img {
    display: block;
    max-width: 200px;
  }
}



// User controls: search, log in, sign in
.user-controls {
  display: flex;
  justify-content: flex-end;
  order: 2;
  // @media(min-width: 1400px) {
  //   order: 1;
  // }
}

// Search
.search__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  padding: 0;

  color: var(--accent-color);

  background: transparent;
  border: none;
  cursor: pointer;

  &:hover {
    color: var(--color);
  }

  svg {
    width: 16px;
    height: 16px;

    fill: currentColor;
  }
}

// Log in and Sign in
.login-links {
  margin-left: 56px;
  .login-mobile {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;
    padding: 0;

    color: var(--accent-color);

    background: transparent;
    border: none;
    cursor: pointer;


    @media(min-width: 1024px) {
      display: none;
    }
  }
  & > div {
    display: none;

    @media(min-width: 1024px) {
      display: block;
    }
  }
}
