// Layout styles

.site-page {
  width: 100%;
  min-height: 100vh;

  @media(min-width: 1024px) {
    display: grid;

    grid-template-areas: "header"
    "main"
    "footer";
    grid-template-rows: 80px 1fr 100px;

    .side-header {
      grid-area: header;
    }

    .side-main {
      grid-area: main;
    }

    .side-footer {
      grid-area: footer;
    }
  }
}

.container {
  margin: 0 auto;

  @media(min-width: 1400px) {
    max-width: 1380px;
  }
}


