.site-footer {
  padding: 20px 30px;

  background-color: var(--light-blue);

  @media(min-width: 1400px) {
    padding: 0;
  }
}

.site-footer-navigation {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  height: 100%;

  @media(min-width: 1400px) {
    display: grid;

    grid-template-columns: 300px 1fr 300px;
  }
}

// Footer logo
.footer-logo {
  order: 1;
}
.footer-logo img {
  display: block;
  width: 97px;
}

.site-footer .menu {
  order: 3;
  margin: 20px auto 5px;

  @media(min-width: 1024px) {
    order: 2;
    margin: 0 auto;
  }
}

.academy-link {
  order: 2;

  text-align: right;

  @media(min-width: 1024px) {
    order: 3;
  }
}
